
#app .goods .bar-secondary{
    bottom:0;
}
.page-goods .content {
    background-color: #f7f7f7;
}
.goods-content-swiper {
    height: 100%;
    padding-bottom: 2rem;
    box-sizing: border-box;
}
.goods-content-swiper>.vux-swiper {
    height: 100%!important;
    overflow-y: scroll;
    box-sizing: border-box;
}
.goods-content-swiper>.vux-swiper .vux-swiper-item{
    box-sizing:border-box;
}
.goods .qrcode-content canvas{
  width:4rem;
  height:4rem;
}
.close-btn{
  width:2rem;
  height:2rem;
  line-height:2rem;
   position:absolute;
  top:-20px;
  right:5px;
  z-index:8888;
  box-shadow:0 0 10px #E53935;
}
.weui-cell__ft{
  //  width:100%;
}



